import Vue from 'vue'
import VueRouter from 'vue-router'
import MyMain from '../views/MyMain.vue'
import MyLogin from '../components/CommonLogin.vue'
import MyIndex from '../views/MyIndex.vue'
import MyYxlm from '../views/MyYxlm.vue'
import MyWzry from '../views/MyWzry.vue'
import MyView from '../views/MyDataview.vue'
// 避免报错
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err)
}
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/index' },
  { path: '/login', name: 'login', component: MyLogin },
  { path: '/index', name: 'index', component: MyIndex },
  { path: '/yxlm', name: 'yxlm', component: MyYxlm },
  { path: '/wzry', name: 'wzry', component: MyWzry },
  { path: '/dataview', name: 'view', component: MyView },
  {
    name: 'back',
    path: '/admin',
    component: MyMain,
    children: [
      {
        path: '/admin',
        name: 'home',
        component: () => import('@/views/MyHome')
      },
      {
        path: '/mall',
        name: 'mall',
        component: () => import('@/views/MyMall')
      },
      {
        path: '/user',
        name: 'user',
        component: () => import('@/views/MyUser')
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})
