<template>
  <header>
    <div class="l-content">
      <el-button
        plain
        icon="el-icon-menu"
        size="mini"
        @click="onMin"
        ref="Minbtn"
      ></el-button>
      <el-breadcrumb>
        <el-breadcrumb-item :to="{ path: '/admin' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="currentMenu.path" v-if="currentMenu">{{
          currentMenu.label
        }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown trigger="hover" @command="handleCommand">
        <span>
          <img :src="userimg" class="userImg" alt="" />
        </span>
        <el-dropdown-menu slot="dropdown" @command="handleCommand">
          <el-dropdown-item command="onUser">个人详情</el-dropdown-item>
          <el-dropdown-item class="logout" command="logout"
            >退出</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </header>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'MyHeader',
  data() {
    return {
      userimg: require('../assets/image/2.jpg')
    }
  },
  methods: {
    onMin() {
      this.$store.commit('onisCollapse')
      console.log(this.$refs.Minbtn)
      if (this.$store.state.isCollapse) {
        document.querySelector('.el-icon-menu').style.transform =
          'rotate(-135deg)'
      } else {
        document.querySelector('.el-icon-menu').style.transform =
          'rotate(270deg)'
      }
    },
    handleCommand(logout) {
      sessionStorage.removeItem('token')
      this.$message({
        message: '退出账号成功',
        type: 'success'
      })
      this.$router.push('/login')
    }
  },
  components: {},
  computed: {
    ...mapState(['currentMenu'])
  }
}
</script>
<style scoped lang="scss">
.el-breadcrumb__item /deep/ {
  .el-breadcrumb__inner,
  .el-breadcrumb__separator {
    font-size: 16px;
    font-weight: 500;
    color: #fff !important;
    text-shadow: 0 0 10px #69e0ff, 0 0 20px #69e0ff, 0 0 40px #69e0ff;
    cursor: pointer !important;
  }
}
header {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: space-between;
}
.l-content {
  display: flex;
  align-items: center;
  .el-button {
    margin-right: 20px;
  }
}
.r-content {
  .userImg {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 40px;
  }
}
</style>
