<script>
// import MyNav from '../components/CommonNav.vue'
export default {
  mounted() {
    this.$router.push('/index#page1')
  },
  name: 'MyIndex',
  data() {
    return {
      options: {
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        afterLoad: this.afterLoad,
        scrollOverflow: true,
        navigation: true, // 是否显示导航，默认为false
        navigationPosition: 'right', // 导航小圆点的位置
        scrollBar: false,
        keyboardScrolling: false, // 是否可以使用键盘方向键导航，默认为true
        continuousVertical: false, // / 是否循环滚动，默认为false。如果设置为true，则页面会循环滚动，而不像loopTop或loopBottom那样出现跳动，注意这个属性和loopTop、loopBottom不兼容和，不要同时设置
        menu: '#menu',
        // navigation: true,
        anchors: ['page1', 'page2', 'page3', 'page4'],
        sectionsColor: [
          '#41b883',
          '#ff5f45',
          '#0798ec',
          '#fec401',
          '#1bcee6',
          '#ee1a59',
          '#2c3e4f',
          '#ba5be9',
          '#b4b8ab'
        ]
      },
      isAside: true,
      url: '@/assets/image/80and55.png',
      onVideo: false,
      footer: false,
      isUserBtn: true,
      isPages: 0
    }
  },
  methods: {
    afterLoad(origin, destination, direction) {
      this.navIndex = destination.index
      this.handleScrolls(destination.index)
      this.isPages = destination.index
    },
    changeVideo() {
      this.onVideo = !this.onVideo
    },
    handleScrolls(pageIndex) {
      // const hash = this.$route.hash
      if (this.$route.path !== '/index') {
        return
      }
      pageIndex === 0
        ? (this.$refs.home.style.top = '200px')
        : (this.$refs.home.style.top = '1500px')
      pageIndex === 1
        ? (this.$refs.yxlm.style.left = '850px')
        : (this.$refs.yxlm.style.left = '1920px')
      pageIndex === 2
        ? (this.$refs.wzry.style.left = '-870px')
        : (this.$refs.wzry.style.left = '-2450px')
      pageIndex === 3
        ? (this.$refs.footer.style.opacity = '100')
        : (this.$refs.footer.style.opacity = '0')
    },
    userbtn() {
      if (this.isUserBtn) {
        this.$refs.footerUser.style.left = '550px'
        this.$refs.footerContent.style.opacity = '100'

        this.isUserBtn = false
      } else {
        this.$refs.footerUser.style.left = '0'
        this.$refs.footerContent.style.opacity = '0'
        this.isUserBtn = true
      }
    }
  },
  computed: {}
}
</script>

