<template>
  <div ref="echarts" style="height: 100%"></div>
</template>
<script>
import * as echarts from 'echarts'
export default {
  mounted() {
    window.onresize = () => {
      this.echart.resize()
      console.log(this.echart)
    }
  },
  props: {
    option: {
      type: Object,
      require: true
    }
  },
  name: 'MyEchart',
  data() {
    return {
      echart: null
    }
  },
  methods: {
    initEchart() {
      this.echart = echarts.init(this.$refs.echarts)
      this.echart.setOption(this.option)
    }
  },
  watch: {
    option: {
      handler() {
        this.initEchart()
      },
      deep: true
    }
  },
  components: {}
}
</script>
<style scoped lang="less"></style>
