                  >
                    <img :src="data.img" alt="" />
                    <div class="shadow skin-shadow">
                      <span class="skin-shadow-text">{{ data.text }}</span>
                    </div>
                  </a>
                </div>
              </div>
              <div class="content-main-item">皮肤</div>
            </div>
          </div>
        </div>
