            >
          </div>
          <div class="pushImg">
            <ul class="pushImg-ul">
              <li v-for="(item, index) in pushImgList" :key="index">
                <a
                  href="javascript:;"
                  class="pushImg-item"
                  @mousemove="onpushImg(item.text)"
                  @mouseleave="onpushImg(false)"
                >
                  <img :src="item.img" alt="" />
                </a>
              </li>
            </ul>
            <p class="push_text" ref="pushText">
              《目标》有人害怕孤独,有人讨厌杀戮,这两个人如果相识,会有怎样的结局。是借助外物的救赎,还是依靠自己的力量…
            </p>
          </div>
        </div>
        <div class="author to" ref="authorBox">
          <div class="skin-box-nav">
            <span class="skin-l el-icon-picture-outline">&nbsp;同人</span>
