      <i
        class="el-icon-close hideVideo sonBox"
        @click="showVideo = !showVideo"
      ></i>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    window.addEventListener('scroll', this.handleScrollx, true)
    this.rotate = this.$store.state.WzryRotate
    this.NewList = this.$store.state.NewList
    this.WzryCard = this.$store.state.WzryCard
    this.WzryVideo = this.$store.state.WzryVideo
    this.skinList = this.$store.state.skinList
    this.pushImgList = this.$store.state.pushImgList
    this.authorList = this.$store.state.authorList
  },
  mounted() {
    this.openweb()
  },
  name: 'MyWzry',
  data() {
    return {
      onitems: 0,
      rotate: [],
      tabList: [
        { title: '热门' },
        { title: '新闻' },
        { title: '公告' },
        { title: '活动' },
        { title: '赛事' }
      ],
      NewList: [],
      WzryCard: [],
      onvideoIndex: 0,
      onskinIndex: 0,
      videoNav: ['王者荣耀', '罗老师谈哲学', '敬请期待'],
      skinNav: ['英雄', '皮肤'],
      WzryVideo: [],
      showVideo: false,
      skinList: [],
      pushImgList: [],
      authorList: []
    }
  },
  methods: {
    to(data, time) {
      setTimeout(() => {
        data.style.transform = 'translateY(0px)'
        data.style.opacity = 100
      }, time)
    },
    onTabs(index) {
      this.onitems = index
      this.$refs.newListBox.style.transform = 'translateX(-' + 20 * index + '%)'
    },
    onvideoNav(index) {
      this.onvideoIndex = index
      if (index === 1) {
        this.WzryVideo = this.$store.state.luoVideo
      } else if (index === 0) {
        this.WzryVideo = this.$store.state.WzryVideo
      } else {
        this.WzryVideo = this.$store.state.jqqdVideo
      }
    },
    isShowVideo(value) {
      if (value.video) {
        this.showVideo = true
        this.$nextTick(() => {
          this.$refs.showVideo.src = value.video
        })
      } else {
        this.$message.error('抱歉哦,暂时没有视频.')
      }
    },
    changeskin(index) {
      this.onskinIndex = index
      if (index === 1) {
        this.$refs.skinBox.style.transform = 'translateX(-' + 50 * index + '%)'
      } else {
        this.$refs.skinBox.style.transform = 'translateX(0)'
      }
    },
    onpushImg(text) {
      if (text === this.$refs.pushText.innerHTML) return
      if (text === false) {
        this.$refs.pushText.innerHTML =
          '《目标》有人害怕孤独,有人讨厌杀戮,这两个人如果相识,会有怎样的结局。是借助外物的救赎,还是依靠自己的力量…'
        return
      }
      this.$refs.pushText.innerHTML = text
    },
    onauthorView(img) {
      if (img === 'no') {
        this.$refs.viewBox.style.opacity = 0
        this.$refs.viewBox.style.transform = 'translateX(0)'
      } else {
        this.$refs.authorView.src = img
        this.$refs.viewBox.style.opacity = 100
        this.$refs.viewBox.style.transform = 'translateX(-390px)'
      }
    },
    openweb() {
      this.to(this.$refs.rotate, 200)
      this.to(this.$refs.teble, 500)
      this.to(this.$refs.video, 800)
      this.to(this.$refs.home, 1000)
      this.to(this.$refs.tansks, 1200)
      this.to(this.$refs.card[0], 800)
      this.to(this.$refs.card[1], 900)
      this.to(this.$refs.card[2], 1000)
      this.to(this.$refs.card[3], 1100)
      setTimeout(() => {
        this.$refs.ai.style.opacity = 100
        this.$refs.ai.style.top = '240px'
      }, 200)
    },
    handleScrollx() {
      console.log(window.pageYOffset)
      if (window.pageYOffset > 150) {
        this.to(this.$refs.videoBox, 300)
        this.to(this.$refs.skinBigBox, 500)
      }
      if (window.pageYOffset > 650) {
        this.to(this.$refs.pushBox, 300)
        this.to(this.$refs.authorBox, 500)
      }
      if (window.pageYOffset > 1200) {
        this.$refs.MyFooter.$refs.footer.style.opacity = '100'
      } else {
        this.$refs.MyFooter.$refs.footer.style.opacity = '0'
      }
    }
  },
  components: {}
}
</script>
