                        <p class="new_date">{{ item.time }}</p>
                      </a>
                    </li>
                    <div class="New_content text-font el-icon-plus">
                      更多资讯
                    </div>
                  </ul>
                </el-tab-pane>
                <el-tab-pane label="公告">
                  <ul class="Carousel-right-ul">
                    <li class="Carousel-right-li">
                      <a class="Carousel-right-a" href="">这是一条新资讯</a>
                    </li>
                  </ul>
                </el-tab-pane>
                <el-tab-pane label="赛事">
                  <ul class="Carousel-right-ul">
                    <li class="Carousel-right-li">
                      <a class="Carousel-right-a" href="">这是一条新资讯</a>
                    </li>
                  </ul>
                </el-tab-pane>
              </el-tabs>
            </el-col>
          </div>
        </el-row>
      </div>
    </main>
    <section>
      <div class="section-logo"></div>
      <div class="section-NEW">
        <aside class="NEW-left">
          <h4 class="NEW-icon"></h4>
          <ul class="NEW-pifu-ul" ref="NEWList">
            <li
              class="NEW-item"
              :class="{ upbox: changeUp == index }"
              v-for="(item, index) in yxlmul"
              :key="index"
              @mousemove="upBox(index)"
              @mouseleave="overBox(index)"
            >
              <a href="javascript:;" class="NEW-item-img">
                <img :src="item.img" alt="" />
                <div class="NEW-item-img-hover" ref="NewHover">
                  <h4>{{ item.title }}</h4>
                  <p>{{ item.hover.content }}</p>
                  <p>{{ item.hover.time }}</p>
                  <i class="bg-shadow"></i>
                </div>
              </a>

              <p class="i">{{ item.title }}</p>
              <span class="NEW-item-time">{{ item.time }}</span>
            </li>
          </ul>
          <div class="contnt-box">
            <div
              class="content-left"
              @mousemove="changeContentHover"
              @mouseleave="NoChangeContentHover"
              ref="contentLeft"
            >
              <img :src="contentLeft.img" alt="" />
              <i class="content-left-shadow"></i>
              <a href="#" class="NEW_lol">{{ contentLeft.NEW }}</a>
              <p class="content-left-title">{{ contentLeft.title }}</p>
              <p class="content-left-content">{{ contentLeft.content }}</p>
              <div class="content-left-hover" ref="contentHover">
                <i class="icon"></i>
                <h4>{{ contentLeft.content }}</h4>
                <a href="#">立即去查看</a>
              </div>
            </div>
            <div class="content-right" ref="contentRight">
              <img :src="contentRight.img" alt="" />
              <i class="content-left-shadow"></i>
              <a href="#" class="NEW_lol">{{ contentRight.NEW }}</a>
              <p class="content-left-title">{{ contentRight.title }}</p>
              <p class="content-left-content">{{ contentRight.content }}</p>
            </div>
          </div>
        </aside>
        <aside class="NEW-right">
          <div class="Author-box">
            <div class="Author" ref="Author">
              <div class="Author-bg">
                <div class="shadow"></div>
                <img :src="author.bg" alt="" />
              </div>
              <div class="header">
                <img :src="author.header" alt="" />
              </div>
              <div class="author-title">{{ author.title }}</div>
              <div class="author-contnent">{{ author.content }}</div>
              <div class="Author-title">作者大大</div>
            </div>
            <div class="inform">
              <ul class="inform-ul">
                <li
                  class="inform-li"
                  v-for="(item, index) in inForm"
                  :key="index"
                  ref="informList"
                  @mouseover="inFormLiHover(index, item.width)"
                  @mouseleave="inFormLiOut(index)"
                >
                  <div class="inform-li-bg">
                    <img :src="item.bg" alt="" />
                  </div>
                  <a
                    href="javascript:;"
                    class="inform-li-strong"
                    :class="item.icon"
                    >{{ item.strong }}</a
                  >
                  <div class="inform-li-score">{{ item.Stars }}</div>
                  <div class="shadow" ref="inFormLi"></div>
                </li>
              </ul>
              <div class="inform-title">各方面学习情况</div>
            </div>
          </div>
          <div class="video-Box" ref="videoBox">
            <div class="video-Box-left">
              <div class="video-Box-left-top">
                <a href="javascript:;" class="NEW-video-box">
                  <img :src="project.img" alt="" class="img NEW-video-bg" />
                  <div class="NEW-video-shadow p_absolute">
                    <p class="p_absolute NEW-video-title">
                      {{ project.title }}
                    </p>
                    <p class="p_absolute NEW-video-content">
                      {{ project.content }}
                    </p>
                  </div>
                </a>
              </div>
              <div class="video-Box-left-bottom">
                <a href="javascript:;" class="NEW-video-box">
                  <img :src="project.img" alt="" class="img NEW-video-bg" />
                  <div class="NEW-video-shadow p_absolute">
                    <p class="p_absolute NEW-video-title">
                      {{ project.title }}
                    </p>
                    <p class="p_absolute NEW-video-content">
                      {{ project.content }}
                    </p>
                  </div>
                </a>
              </div>
            </div>
            <div class="video-Box-right">
              <div class="bilibili-video">
                <a href="javascript:;" class="video-bg">
                  <img :src="bilibili.videoBg" alt="" class="img" />
                </a>
                <i class="shadow"></i>
                <p class="title">{{ bilibili.title }}</p>
                <div class="shadow-bg">
                  <p class="shadow-bg-title p_absolute">
                    {{ bilibili.shadow_title }}
                  </p>
                  <p class="shadow-bg-content p_absolute">
                    {{ bilibili.shadow_content }}
                  </p>
                </div>
              </div>
              <div class="bilibili-content">
                <a :href="bilibili.url" class="bilibili">
                  <i href="javascript:;" class="bilibili-icon">
                    <img :src="bilibili.bilibiliIcon" alt="" class="img" />
                  </i>
                  <p href="javascript:;" class="gobilibili">点击前往观看</p>
                </a>
              </div>
            </div>
          </div>
        </aside>
        <my-footer ref="MyFooter"></my-footer>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  created() {
    window.addEventListener('scroll', this.handleScrollx, true)
    this.contentLeft = this.$store.state.lolContentLeft
    this.contentRight = this.$store.state.lolContentRight
    this.author = this.$store.state.author
    this.inForm = this.$store.state.inForm
    this.bilibili = this.$store.state.bilibili
    this.project = this.$store.state.project
  },
  mounted() {},
  name: 'MyYxlm',
  data() {
    return {
      changeUp: null,
      contentLeft: {},
      contentRight: {},
      author: {},
      inForm: [],
      bilibili: {},
      project: {}
    }
  },
  methods: {
    upBox(index) {
      this.changeUp = index
      this.$refs.NewHover[index].style.display = 'block'
    },
    overBox(index) {
      this.changeUp = null
      this.$refs.NewHover[index].style.display = 'none'
    },
    changeContentHover() {
      this.$refs.contentHover.style.opacity = 100
    },
    NoChangeContentHover() {
      this.$refs.contentHover.style.opacity = 0
    },
    inFormLiHover(index, width) {
      this.$refs.inFormLi[index].style.width = width + '%'
    },
    inFormLiOut(index) {
      this.$refs.inFormLi[index].style.width = '0px'
    },
    handleScrollx() {
      if (window.pageYOffset > 300) {
        this.$refs.Carousel.style.opacity = '100'
        this.$refs.mainLeft.style.transform = 'translateX(0px)'
        this.$refs.mainRight.style.transform = 'translateX(0px)'
      } else {
        this.$refs.Carousel.style.opacity = '0'
        this.$refs.mainLeft.style.transform = 'translateX(-270px)'
        this.$refs.mainRight.style.transform = 'translateX(270px)'
      }
      if (window.pageYOffset > 1000) {
        this.$refs.NEWList.style.opacity = '100'
        this.$refs.NEWList.style.transform = 'translateX(0px)'
        this.$refs.Author.style.opacity = '100'
        this.$refs.Author.style.transform = 'translateX(0px)'
        this.$refs.informList[0].style.transform = 'translateX(0px)'
        this.$refs.informList[0].style.opacity = '100'
        setTimeout(() => {
          this.$refs.informList[1].style.transform = 'translateX(0px)'
          this.$refs.informList[1].style.opacity = '100'
        }, 200)
        setTimeout(() => {
          this.$refs.informList[2].style.transform = 'translateX(0px)'
          this.$refs.informList[2].style.opacity = '100'
        }, 400)
        setTimeout(() => {
          this.$refs.informList[3].style.transform = 'translateX(0px)'
          this.$refs.informList[3].style.opacity = '100'
        }, 600)
      } else {
        this.$refs.NEWList.style.opacity = '0'
        this.$refs.NEWList.style.transform = 'translateX(-300px)'
        this.$refs.Author.style.opacity = '0'
        this.$refs.Author.style.transform = 'translateX(300px)'
        this.$refs.informList[0].style.transform = 'translateX(200px)'
        this.$refs.informList[0].style.opacity = '0'
        this.$refs.informList[1].style.transform = 'translateX(200px)'
        this.$refs.informList[1].style.opacity = '0'
        this.$refs.informList[2].style.transform = 'translateX(200px)'
        this.$refs.informList[2].style.opacity = '0'
        this.$refs.informList[3].style.transform = 'translateX(200px)'
        this.$refs.informList[3].style.opacity = '0'
      }
      if (window.pageYOffset > 1400) {
        this.$refs.contentLeft.style.opacity = '100'
        this.$refs.contentLeft.style.transform = 'translateY(0px)'
        setTimeout(() => {
          this.$refs.contentRight.style.opacity = '100'
          this.$refs.contentRight.style.transform = 'translateY(0px)'
          this.$refs.videoBox.style.opacity = '100'
        }, 200)
      } else {
        this.$refs.contentLeft.style.opacity = '0'
        this.$refs.contentLeft.style.transform = 'translateY(300px)'
        this.$refs.contentRight.style.opacity = '0'
        this.$refs.contentRight.style.transform = 'translateY(300px)'
        this.$refs.videoBox.style.opacity = '0'
      }

      if (window.pageYOffset > 1600) {
        this.$refs.MyFooter.$refs.footer.style.opacity = '100'
      } else {
        this.$refs.MyFooter.$refs.footer.style.opacity = '0'
      }
    }
  },
  components: {},
  computed: {
    yxlmList() {
      return this.$store.state.lolList
    },
    yxlmtitle() {
      return this.$store.state.loltitle
    },
    yxlmul() {
      return this.$store.state.lolul
    }
  }
}
</script>
