<template>
  <header>
    <div class="logo" @click="this.$router.push('/index')">
      <img src="../assets/image/80and55.png" alt="" />
    </div>
    <ul class="nav">
      <li v-for="(item, index) in navList" :key="index" @click="toPath(item)">
        {{ item.label }}
      </li>
    </ul>
    <div class="avatar" @click="loginin">
      <span class="user">{{ UserInfo.name }}</span>
      <el-avatar
        :size="50"
        src="https://pic.qqtn.com/up/2017-12/15139098069689467.jpg"
      ></el-avatar>
    </div>
    <div class="ewm"></div>
  </header>
</template>
<script>
export default {
  mounted() {
    window.onbeforeunload = function () {
      localStorage.removeItem('token')
    }
  },
  name: 'MyNav',
  data() {
    return {
      txImg: '../assets/image/wx.jpg'
    }
  },
  methods: {
    toPath(path) {
      this.$router.push({ name: path.name })
    },
    loginin() {
      this.$router.push({ name: 'login' })
    }
  },
  components: {},
  computed: {
    navList() {
      return this.$store.state.navList
    },
    UserInfo() {
      return this.$store.state.user
    }
  }
}
</script>
<style scoped lang="less">
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 65px;
  display: flex;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9;
  .logo {
    flex: 1;
    padding-left: 6vw;
  }
  .avatar {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 3vw;
    cursor: pointer;
    .user {
      margin-right: 20px;
      font-size: 16px;
      color: #fff;
    }
  }
  .nav {
    display: flex;
    flex: 8;
    justify-content: flex-end;
    padding-right: 4vw;
    li {
      list-style: none;
      margin-left: 20px;
      font-size: 18px;
      color: #fff;
      font-weight: 700;
      cursor: pointer;
    }
  }
}
@keyframes lang {
  0% {
    transform: translateX(-1780px);
  }
  100% {
    transform: translateX(0);
  }
}
.ewm {
  position: fixed;
  top: 0px;
  right: 20px;
  width: 65px;
  height: 65px;
  transform: translateX(-1780px);
  border: 1px solid #fff;
  background: url('../assets/image/lang.gif') no-repeat;
  background-size: 100%;
  animation: lang 10s infinite linear alternate-reverse;
  box-sizing: border-box;
}
</style>
