<template>
  <div class="loginBox" @mousemove="onMouseMove" ref="boxBg">
    <header>
      <span class="logo"><img src="../assets/image/80and55.png" alt="" /></span>
      <h4 class="title">
        Welcome&nbsp; to &nbsp;&nbsp;呆龙----Demo
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;请先登录哦!
      </h4>
      <button class="mroe-btn" @click="mroeShow = !mroeShow">更多选项</button>
      <div class="mroeBox">
        <el-collapse-transition>
          <div v-show="mroeShow">
            <div class="transition-box transition-box-item">联系我们</div>
            <div class="transition-box transition-box-item">关于我们</div>
            <div
              class="transition-box transition-box-item"
              style="cursor: pointer"
              @click="look = !look"
            >
              观看壁纸
            </div>
          </div>
        </el-collapse-transition>
      </div>
    </header>
    <div class="body" v-show="look">
      <el-row type="flex" justify="center">
        <el-col :span="10">
          <transition name="el-zoom-in-top">
            <div v-show="isUp" class="transition-box" style="opacity: '50%'">
              <el-card shadow="always" :body-style="{ padding: '0px' }">
                <el-row type="flex" justify="center">
                  <el-col :span="16" class="left-box"
                    ><div class="title-logo">
                      <img src="../assets/image/80and55.png" alt="" />
                    </div>
                    <div style="width: 400px">
                      <el-form
                        label-position="top"
                        label-width="60px"
                        :rules="rules"
                        :model="upForm"
                        ref="upFrom"
                      >
                        <el-form-item label="邮箱" prop="email">
                          <el-input
                            placeholder="请输入邮箱"
                            v-model="upForm.email"
                          ></el-input>
                        </el-form-item>
                        <el-form-item label="密码" prop="password">
                          <el-input
                            type="password"
                            placeholder="请输入密码"
                            v-model="upForm.password"
                          ></el-input>
                        </el-form-item>
                        <el-form-item>
                          <el-button label-width="100px" @click="loginIn"
                            >登录</el-button
                          >
                        </el-form-item>
                      </el-form>
                    </div>
                  </el-col>
                  <el-col :span="8" class="right-box">
                    <h4 class="min-title">还未注册吗</h4>
                    <p
                      style="
                        color: #fff;
                        margin-top: 50px;
                        text-align: center;
                        font-size: 16px;
                      "
                    >
                      点击下方注册，有你有我，一鼓作气
                    </p>
                    <div class="up-btn" @click="btnChange">点击注册</div>
                  </el-col>
                  <!-- <el-col :span="16" class="up-box" v-if="!isUp"> </el-col> -->
                </el-row>
              </el-card>
            </div>
          </transition>
          <transition name="el-zoom-in-bottom">
            <div v-show="!isUp" class="transition-box upFrom">
              <el-card shadow="always" :body-style="{ padding: '0px' }">
                <el-row type="flex" justify="center">
                  <el-col :span="8" class="right-box-up">
                    <h4 class="min-title">注册账号</h4>
                    <p
                      style="
                        color: #fff;
                        margin-top: 50px;
                        text-align: center;
                        font-size: 16px;
                      "
                    >
                      加入我们，一起学习。今天的努力是为了成就明天的自己
                    </p>
                    <div class="up-btn" @click="btnChange">切换回登录页面</div>
                  </el-col>
                  <el-col :span="16" class="left-box"
                    ><div class="title-logo-add">
                      <img src="../assets/image/80and55.png" alt="" />
                    </div>
                    <div style="width: 400px">
                      <el-form
                        label-position="top"
                        label-width="60px"
                        :rules="addrules"
                        :model="addForm"
                        ref="addFrom"
                      >
                        <el-form-item label="用户名" prop="addname">
                          <el-input
                            placeholder="请输入用户名"
                            v-model.trim="addForm.addname"
                          ></el-input>
                        </el-form-item>
                        <el-form-item label="账号" prop="addemail">
                          <el-input
                            placeholder="请输入邮箱"
                            v-model="addForm.addemail"
                            @blur="verifyEmail"
                          ></el-input>
                        </el-form-item>
                        <el-form-item label="密码" prop="addpassword">
                          <el-input
                            type="password"
                            placeholder="请输入密码"
                            v-model="addForm.addpassword"
                          ></el-input>
                        </el-form-item>
                        <el-form-item>
                          <el-button label-width="100px" @click="onAddNewuser"
                            >注册账号</el-button
                          >
                        </el-form-item>
                      </el-form>
                    </div>
                  </el-col>
                </el-row>
              </el-card>
            </div>
          </transition>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: '',
  data() {
    // 账号的校验
    return {
      rules: {
        email: [
          { required: true, message: '邮箱不能为空哦' },
          {
            type: 'email',
            message: '请输入正确的邮箱地址呢',
            trigger: 'blur'
          }
        ],
        name: [
          { required: true, message: '用户名不能为空哦' },
          {
            message: '请输入正确的用户名 !',
            max: 10,
            min: 3,
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '密码不能为空哦',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 18,
            message: '请输入正确的密码哦',
            trigger: 'blur'
          }
        ]
      },
      addrules: {
        addemail: [
          { required: true, message: '邮箱不能为空哦' },
          {
            type: 'email',
            message: '请输入正确的邮箱地址呢',
            trigger: 'blur'
          }
        ],
        addname: [
          { required: true, message: '用户名不能为空哦' },
          {
            message: '请输入正确的用户名 !',
            max: 10,
            min: 3,
            trigger: 'blur'
          }
        ],
        addpassword: [
          {
            required: true,
            message: '密码不能为空哦',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 18,
            message: '请输入正确的密码哦',
            trigger: 'blur'
          }
        ]
      },
      isUp: true,
      mroeShow: false,
      look: true,
      id: null,
      addForm: {
        addname: '',
        addemail: '',
        addpassword: ''
      },
      upForm: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    onMouseMove(e) {
      const x = -e.clientX / 8
      const y = -e.clientY / 8
      this.$refs.boxBg.style.backgroundPositionX = x + 'px'
      this.$refs.boxBg.style.backgroundPositionY = y + 'px'
    },
    loginIn() {
      this.$refs.upFrom.validate((valida) => {
        if (!valida) return
        this.$http({
          type: 'get',
          url: 'http://localhost:3001/user?email_like=' + this.upForm.email
        }).then((response) => {
          console.log(response.data[0])
          if (response && response.data[0].password === this.upForm.password) {
            this.$router.push({ name: 'index' })
            sessionStorage.setItem('token', 'dailong')
            this.$store.commit('onUser', response.data[0])
          } else {
            this.$message({
              message: '邮箱或密码错误,请重新输入 !',
              type: 'error'
            })
          }
        })
      })
    },
    onAddNewuser() {
      this.$refs.addFrom.validate((valida) => {
        if (!valida) return
        this.$http({
          type: 'get',
          url: 'http://localhost:3001/user?email_like=' + this.addForm.addemail
        }).then((response) => {
          console.log(response)
          if (response.data.length !== 0) {
            this.$message.error('邮箱已被注册 !')
          } else {
            this.$http
              .post('http://localhost:3001/user', {
                name: this.addForm.addname,
                email: this.addForm.addemail,
                password: this.addForm.addpassword,
                role: '普通用户'
              })
              .then((response) => {
                console.log(response)
              })
            this.$message({
              message: '账号注册成功 !',
              type: 'success'
            })
          }
        })
      })
    },
    btnChange() {
      this.$refs.boxBg.style.transition = 'all 0.6s'
      setTimeout(() => {
        this.$refs.boxBg.style.transition = ''
      }, 200)
      this.isUp = !this.isUp
      if (!this.isUp) {
        this.$refs.boxBg.className = 'loginBoxFrom'
      } else {
        this.$refs.boxBg.className = 'loginBox'
      }
      this.$refs.addFrom.resetFields()
      this.$refs.upFrom.resetFields()
    },
    async verifyEmail() {
      const { data } = await this.$http({
        type: 'get',
        url: 'http://localhost:3001/user?email_like=' + this.addForm.addemail
      })
      console.log(data)
      if (data.length !== 0) {
        this.$message.error('邮箱已被注册 !')
      }
    }
  },

  components: {}
}
</script>
<style scoped lang="less">
.el-card {
  box-sizing: border-box;
  height: 50vh;
}
.loginBox {
  height: 100vh;
  width: 100vw;
  background: url('../assets/image/bg.jpg') no-repeat;
  background-size: 120%;
}
.loginBoxFrom {
  height: 100vh;
  width: 100vw;
  background: url('../assets/image/bg2.jpg') no-repeat;
  background-size: 120%;
}
header {
  z-index: 1;
  height: 65px;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 200px;
}
.logo {
  height: 55px;
  border-right: 20px;
  overflow: hidden;
  margin-left: 50px;
  border-radius: 5px;
  animation: bg 20s infinite ease-in-out;
}
@keyframes bg {
  0% {
    background-color: rgb(158, 203, 255);
  }
  10% {
    background-color: rgb(255, 184, 219);
  }
  20% {
    background-color: rgb(189, 255, 232);
  }
  30% {
    background-color: rgb(184, 196, 255);
  }
  40% {
    background-color: rgb(255, 204, 246);
  }
  50% {
    background-color: rgb(179, 244, 255);
  }
  60% {
    background-color: rgb(189, 255, 246);
  }
  70% {
    background-color: rgb(255, 168, 226);
  }
  80% {
    background-color: rgb(255, 255, 168);
  }
  90% {
    background-color: rgb(240, 179, 255);
  }
  100% {
    background-color: rgb(158, 203, 255);
  }
}
.title {
  color: #fff;
  font-size: 22px;
  cursor: pointer;
}
.title-logo,
.title-logo-add {
  width: 90px;
  height: 55px;
  background-color: #74d1f3;
  margin: 40px 0 40px 165px;
  margin-bottom: 20px;
  text-align: center;
  border-radius: 5px;
}
.title-logo-add {
  margin: 0 0 0 155px;
  background-color: #f2b75d;
}
.right-box {
  background: url('../assets/image/wzry.jpg') no-repeat;
  background-size: 100%;
  height: 50vh;
}
.right-box-up {
  background: url('../assets/image/yxlm.jpg') no-repeat;
  background-size: 110%;
  height: 50vh;
}
.left-box {
  padding: 40px;
}
.min-title {
  font-size: 30px;
  color: #fff;
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
  margin-top: 80px;
}
.up-btn {
  margin: 100px auto;
  text-align: center;
  line-height: 40px;
  width: 120px;
  height: 40px;
  border: 1px solid #fff;
  border-radius: 50px;
  color: #fff;
  cursor: pointer;
  user-select: none;
  font-size: 16px;
}
.el-button {
  width: 337px;
  margin-top: 30px;
  background-color: #ccc;
}
.el-form-item {
  text-align: center;
}
/deep/ .el-input__inner {
  border: 0;
  border-bottom: 2px solid #ccc;
  border-radius: 0;
}
/deep/ .el-form--label-top .el-form-item__label {
  padding: 0;
}
/deep/
  .el-form-item.is-required:not(.is-no-asterisk)
  > .el-form-item__label:before {
  content: '';
}
.upFrom {
  position: absolute;
  top: 0px;
  left: 560px;
  width: 800px;
}
.mroeBox {
  position: absolute;
  top: 50px;
  right: 28px;
  width: 100px;
  text-align: center;
  border: 1px solid #fff;
  border-radius: 10px;
  .transition-box-item {
    height: 40px;
    line-height: 40px;
    color: #fff;
  }
}
.mroe-btn {
  color: #fff;
  background-color: #87ceeb;
  margin-right: 40px;
  width: 80px;
  height: 30px;
  border: 0;
}
</style>
