<template>
  <div>
    <el-menu
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      :collapse="newisCollapse"
      background-color="#545c64"
      text-color="#ccc"
      active-text-color="rgb(46, 199, 201)"
    >
      <div class="logo" v-if="!newisCollapse">
        <img
          class="imgs"
          src="../assets/image/大边框.png"
          alt=""
          @click="onDataView"
        />
        <span>龙管理-后台</span>
      </div>
      <div class="logo" v-if="newisCollapse">
        <span style="height: 60px">——呆龙——</span>
      </div>
      <el-menu-item
        :index="item.path"
        v-for="item in noChildren"
        :key="item.path"
        @click="onpath(item)"
      >
        <i :class="'el-icon-' + item.icon"></i>
        <span slot="title">{{ item.label }}</span>
      </el-menu-item>
      <el-submenu
        :index="item.label"
        v-for="item in isChildren"
        :key="item.path"
      >
        <template slot="title">
          <i :class="'el-icon-' + item.icon"></i>
          <span slot="title">{{ item.label }}</span>
        </template>
        <el-menu-item-group>
          <el-menu-item
            v-for="item in item.children"
            :index="item.label"
            :key="item.path"
            @click="onpath(item)"
            ><i :class="'el-icon-' + item.icon"></i>
            <span slot="title">{{ item.label }}</span></el-menu-item
          >
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>
<script>
export default {
  name: 'MyAside',
  data() {
    return {
      menu: [
        {
          path: '/admin',
          name: 'home',
          label: '首页',
          icon: 's-home',
          url: 'Home/Home'
        },
        {
          path: '/mall',
          name: 'mall',
          label: '商品管理',
          icon: 'video-play',
          url: 'MallManage/MallManage'
        },
        {
          path: '/user',
          name: 'user',
          label: '用户管理',
          icon: 'user',
          url: 'UserManage/UserManage'
        },
        {
          label: '其他',
          icon: 'location',
          children: [
            {
              path: '/page1',
              name: 'page1',
              label: '页面1',
              icon: 'setting',
              url: 'Other/PageOne'
            },
            {
              path: '/page2',
              name: 'page2',
              label: '页面2',
              icon: 'setting',
              url: 'Other/PageTwo'
            }
          ]
        }
      ]
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    onpath(item) {
      this.$router.push({ name: item.name })
      this.$store.commit('selectMenu', item)
    },
    onDataView() {
      this.$router.push('/dataview')
    }
  },
  computed: {
    noChildren() {
      return this.menu.filter((item) => !item.children)
    },
    isChildren() {
      return this.menu.filter((item) => item.children)
    },
    newisCollapse() {
      return this.$store.state.isCollapse
    }
  }
}
</script>
<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
}
.el-menu {
  min-height: 100vh;
}
.logo {
  display: flex;
  justify-content: space-around;
  margin: 5px 10px;
}
.imgs {
  border-radius: 20px;
}
.logo span {
  flex: 0.9;
  align-self: center;
  text-align: center;
  font-size: 16px;
  color: #fcf9fe;
  font-weight: 500;
  text-shadow: 0 0 10px #69e0ff, 0 0 20px #69e0ff, 0 0 40px #69e0ff;
}
</style>
