<template>
  <div class="tabs">
    <el-tag
      v-for="(item, index) in tagList"
      :key="item.name"
      :closable="item.name !== 'home'"
      :effect="$route.name === item.name ? 'light' : 'plain'"
      @click="changeTag(item)"
      @close="handleClose(item, index)"
      >{{ item.label }}</el-tag
    >
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: '',
  data() {
    return {}
  },
  methods: {
    ...mapMutations(['removeTab', 'selectMenu']),
    changeTag(item) {
      this.$router.push(item.path)
      this.$store.commit('selectMenu', item)
    },
    handleClose(tag, index) {
      const length = this.tagList.length - 1
      this.removeTab(tag)
      // this.$store.commit('selectMenu', tag)
      if (tag.name !== this.$route.name) {
        return
      }
      if (index === length) {
        // 往左边跳转链接
        this.$router.push({
          name: this.tagList[index - 1].name
        })
        this.selectMenu(this.tagList[index - 1])
      } else {
        // 往右边跳转链接
        this.$router.push({ name: this.tagList[index].name })
      }
    }
  },
  components: {},
  computed: {
    ...mapState(['tagList'])
  }
}
</script>
<style scoped lang="less">
.tabs {
  margin: 10px 10px 0 15px;
  .el-tag {
    margin-right: 20px;
    cursor: pointer;
  }
}
</style>
